<template>
 <div data-app class="col-xs-12 col-md-12" v-if="ready">
   <div class="flex px0">
      <div class="col-7" style="padding-left:0">
        <input type='text' placeholder="Live Search Field" class='form-control' style='margin-bottom:0.5em' v-model="search_key" @keyup="search_key_data"/> 
      </div>
      <div class="col-2">
      </div>
      <div class="col-3 px0 flex">
          <div class="col-6 pl0">
            <input type="number" class="form-control" v-model="st_yr" @change="update_period()"/>
          </div>
          <div class="col-6 pr0">
            <input type="number" class='form-control' v-model="ed_yr" readonly/>
          </div>
      </div>
   </div>  
   <div class="table-responsive">
    <v-data-table
      :headers="headers"
      :items="pooldata"
      class="elevation-1"
      :rows-per-page-items="[20]"
      disable-initial-sort
    >
      <template v-slot:items="props">
      <td style='text-align:center'><a href="" :id= props.item.item_id v-on:click.prevent="editform($event)" style='font-size:1.3rem'><i class="fa fa-pencil-square-o" aria-hidden="true"></i></a></td>
      <td style='width:5em'>{{ props.item.ref_id }}</td>
      <td>{{ props.item.Req_Name }}</td>
      <td style='width:55em'>{{ props.item.Description }}</td>
      <td>{{ props.item.Location }}</td>
      <td>{{ props.item.Due_date }}</td>
      <td style='width:5em'>{{ props.item.Due_time }}</td>
      <td>{{ props.item.Handler }}</td>
      <td>{{ props.item.Status }}</td>
    
      <template v-slot:pageText="props">
        Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
      </template>
    
    </template>
    </v-data-table>
</div>
</div>
</template>

<script>
import axios from 'axios';

export default {
    data(){
        return{
          ready:false,
          st_yr:0,
          ed_yr:0,
          search_key: '',
          pooldata : [],
          userid:"",
          headers: [
            { text: 'Action',value :'id',sortable: false},
            { text: 'ID',value :'item_id',sortable: true},
            { text: 'Requester',value:'Req_Name',sortable: true},
            { text: 'Description',value:'Description',sortable: true},
            { text: 'Location',value:'Location',sortable: true},
            { text: 'Due date',value:'Due_date',sortable: true},
            { text: 'Due Time',value:'Due_time',sortable: false},
            { text: 'Assigned to',value:'Handler',sortable: true},
            { text: 'Issue Status',value:'Status',sortable: true},
          ],
        }        
    },
    async created(){
        this.get_period();
        var formData = new FormData();
        formData.append('key',this.search_key);
        formData.append('st',this.st_yr);
        formData.append('ed',this.ed_yr);
        await axios.post('RequestPortal/backend/IR.php',formData)
          .then(
          response=>{
              this.pooldata = response.data
          }
        );    
        this.search_key = document.getElementById('query').value;  
        await this.search_key_data(); 
        this.ready=true;
    },
    mounted(){
      window.addEventListener('focus', this.search_key_data);
    },
    beforeDestroy() {
      window.removeEventListener('focus', this.search_key_data);
    },
    methods:{
        update_period(){
          this.ed_yr = parseInt(this.st_yr)+1;
          this.$forceUpdate();
          this.search_key_data();
        },
        get_period(){
          var dateObj = new Date();
          var month = dateObj.getUTCMonth() + 1; //months from 1-12
          var year = dateObj.getUTCFullYear();
          if(month < 9){
            this.st_yr = year-1;
            this.ed_yr = year;
          } else {
            this.st_yr = year;
            this.ed_yr = year + 1;
          }
        },
        editform(event){
            var targetID = event.currentTarget.id;
            document.getElementById('tmp_id').value = targetID;
            document.getElementById('tmp_id_2').value = 'handler';
            document.getElementById('entry_type').value = 'IR';
            document.getElementById('query').value = this.search_key;
            //this.$router.push('edit_form');
            this.$router.push('ticket');
        },
            async search_key_data(){
            var formData = new FormData();
            formData.append('key',this.search_key);
            formData.append('st',this.st_yr);
            formData.append('ed',this.ed_yr);
            await axios.post('RequestPortal/backend/IR.php',formData)
            .then(
            response=>{
                this.pooldata = response.data;
            }
        );   
        }

    }
}
</script>
<style scoped>
  .pl0 {
    padding-left:0!important;
  }
  .pr0 {
    padding-right:0!important;
  }
</style>



